import React from 'react'
import './Payform.css';
import Paysuccess from './Paysuccess';
import { useState } from "react";


export default function Payform(props) {
    const [paySuccessPopup, setPaySuccessPopup] = useState(false);
    return (
        <div className="paybody">
            <div className="container p-0">
                <div className="card px-4">
                    <p className="h8 py-3">Payment Details</p>
                    <div className="row gx-3">
                        <div className="col-12">
                            <div className="d-flex flex-column">
                                <p className="text mb-1">Person Name</p> <input className="payformStyle form-control mb-3" type="text" placeholder="Name"/>
                            </div>
                        </div>
                        <div className="col-12">
                            <div className="d-flex flex-column">
                                <p className="text mb-1">Card Number</p> <input className="payformStyle form-control mb-3" type="text" placeholder="1234 5678 435678"/>
                            </div>
                        </div>
                        <div className="col-6">
                            <div className="d-flex flex-column">
                                <p className="text mb-1">Expiry</p> <input className="payformStyle form-control mb-3" type="text" placeholder="MM/YYYY"/>
                            </div>
                        </div>
                        <div className="col-6">
                            <div className="d-flex flex-column">
                                <p className="text mb-1">CVV/CVC</p> <input className="payformStyle form-control mb-3 pt-2 " type="password" placeholder="***"/>
                            </div>
                        </div>
                        <div className="col-12">
                            <div className="btn btn-primary mb-3 btnpay"  onClick={(e) =>{
                               e.preventDefault();
                               setPaySuccessPopup(true)}}> <span className="ps-3"></span>{props.rate}<span className="fas fa-arrow-right"></span> 
                            </div>
                        </div>
                        <Paysuccess trigger={paySuccessPopup} setTrigger={setPaySuccessPopup}/>
                    </div>
                </div>
            </div>
        </div>
    )
}
