import React from 'react'

export const Login = () => {
    let myStyle = {
        width: "50%",
        backgroundColor: "white",
        borderRadius: "12px",
        padding: "30px",
        boxShadow: "10px 10px 5px #aaaaaa",
        marginTop: "80px"
    }
    return (
        <div className="container" style={myStyle}>
            <h2>Log in</h2>
            <form>
                <div className="mb-3">
                    <label htmlFor="email" className="form-label">Email address</label>
                    <input type="text" className="form-control" id="email" placeholder="Enter your email" required/>
                    <small id="emailvalid" className="form-text   text-muted invalid-feedback">
                        Your email must be a valid email
                    </small>
                </div>
                <div className="mb-3">
                    <label htmlFor="password" className="form-label">Password</label>
                    <input type="password" className="form-control" placeholder="Enter your password" id="password" required/>
                </div>
                <button id="submit" className="btn btn-outline-info">Log in</button>
            </form>
        </div>
    )
}
