// import logo from './logo.svg';
import React,{useState} from 'react'
import './App.css';
import Header from "./element/Header";
import {Foot} from "./element/Foot";
import {HeroHeader} from "./element/HeroHeader";
import {Login} from './element/Login';
import Signup from './element/Signup';
import { BrowserRouter as Router, Routes, Route} from "react-router-dom";


function App() {
  const [string, setString] = useState("");

  return (
      <>
       <Router>      
         <Header title="BookMyRoom" string={string} updateString={setString}/>
         <Routes>
           <Route path="/" element={<HeroHeader string={string}/>}/>
           <Route path="/login" element={<Login/>}/>
           <Route path="/signup" element={<Signup/>}/>
        </Routes>
       </Router>
       <Foot/>
      </>
  );
}

export default App;
