import React, {useEffect, useState} from "react";
import HotelsItem from "./HotelsItem";
import { hotels } from "./Hoteldata.js";

function Hotels(props) {
    const [currentHotels, setCurrentHotels] = useState(hotels);
    
    let result =hotels.filter(hotel => { if(hotel.name.toLowerCase().includes(props.string.toLowerCase()))
      return hotel})
        useEffect(() => {
          setCurrentHotels(()=>result);
            // console.log(currentHotels)
        }, [props.string])
        
  return (
    
    <div className="container my-3">
      <h2 className="text-center">Hotels</h2>
      <div>
        <div className="d-flex" style={{flexWrap: "wrap"}}>
          {currentHotels.map((hotel) => (
            <HotelsItem hotel={hotel}/>
          ))}        
        </div>
      </div>
    </div>
  );
}

export default Hotels;
