import React from 'react'
import BookNow from './BookNow';
import { useState } from "react";
import Payment from './Payment';
import Payform from './Payform';


function HotelsItem(props) {
    const [buttonPopup, setButtonPopup] = useState(false);
    const [btnPopup, setBtnPopup] = useState(false);
    return (
        <div>
            <div className="card my-3 mx-3" style={{ width: "18rem" }}>
                <img src={props.hotel.imageUrl} className="card-img-top" alt="..." />
                <div className="card-body">
                    <h5 className="card-title">{props.hotel.name}, {props.hotel.location}</h5>
                    <p className="card-text">{props.hotel.facilities}</p>
                    <button className="btn btn-primary btn-sm" onClick={() => setButtonPopup(true)}>Book Now</button>
                    <BookNow trigger={buttonPopup} setTrigger={setButtonPopup} rate={props.hotel.rupees}>
                        <form>
                            <div className="mb-3">
                                <label htmlFor="exampleInputEmail1" className="form-label">Check in Date</label>
                                <input type="date" className="form-control" id="exampleInputEmail1" aria-describedby="emailHelp" required />
                                <label htmlFor="exampleInputEmail1" className="form-label">Check out Date</label>
                                <input type="date" className="form-control" id="exampleInputEmail1" aria-describedby="emailHelp" required />
                                <div>Adult</div>
                                <select className="form-select" aria-label="Default select example" required>
                                    <option selected>Select</option>
                                    <option value="1">1</option>
                                    <option value="2">2</option>
                                    <option value="3">3</option>
                                    <option value="4">4</option>
                                    <option value="5+">5+</option>
                                </select>
                                <div>Children</div>
                                <select className="form-select" aria-label="Default select example" required>
                                    <option selected>Select</option>
                                    <option value="1">1</option>
                                    <option value="2">2</option>
                                </select>
                                <div>Include Lunch</div>
                                <select className="form-select" aria-label="Default select example" required>
                                    <option selected>Select</option>
                                    <option value="1">Yes</option>
                                    <option value="2">No</option>
                                </select>
                            </div>
                            <button type="submit" className="btn btn-primary" onClick={(e) => {
                                e.preventDefault();
                                setBtnPopup(true)
                            }}>Continue to payment</button>
                        </form>
                    </BookNow>
                    <Payment trigger={btnPopup} setTrigger={setBtnPopup}>
                        <Payform rate={props.hotel.rupees} />
                    </Payment>
                </div>
            </div>
        </div>
    )
}

export default HotelsItem

