import React from 'react'
import { Link } from "react-router-dom";

const Header = (props) => {
    const navStyle = {
        backgroundColor: "white",
        position: "fixed",
        width: "100%",
        zIndex: "1",
        top: "0"
    }
    function searching(e){
        e.preventDefault()
        props.updateString(e.target.value)
    }

    return (
        <div style={navStyle}>
            <nav className="navbar navbar-expand-lg navbar-light">
                <div className="container-fluid">
                    <Link className="navbar-brand titleStyle" to="#">{props.title}</Link>
                    <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
                        <span className="navbar-toggler-icon"></span>
                    </button>
                    <div className="collapse navbar-collapse" id="navbarSupportedContent">
                        <ul className="navbar-nav me-auto mb-2 mb-lg-0">
                            <li className="nav-item">
                                <Link style={{ color: "black" }} className="nav-link" aria-current="page" to="/">Home</Link>
                            </li>
                            <li className="nav-item">
                                <Link style={{ color: "black" }} className="nav-link" to="/login">Log in</Link>
                            </li>
                            <li className="nav-item">
                                <Link style={{ color: "black" }} className="nav-link" to="/signup">Sign up</Link>
                            </li>
                            <li className="nav-item">
                                <Link style={{ color: "black" }} className="nav-link" to="/contact">Contact us</Link>
                            </li>
                        </ul>
                        <form className="d-flex">
                            <input className="form-control me-2" type="search" placeholder="Search" aria-label="Search" onChange={(e)=> searching(e)} />
                        </form>
                    </div>
                </div>
            </nav>
        </div>
    )
}

export default Header
